.p-pdf-selectPdf .hjelpetekst {
  position: absolute;
  right: 2rem;
  margin-top: 1rem;
}
.a-pdf-editPdf .hiding {
  visibility: none;
}
.a-pdf-editPdf .documentbox {
  display: flex;
  flex-direction: column;
}
.a-pdf-editPdf .documentbox-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.a-pdf-editPdf .hjelpetekst {
  position: absolute;
  right: 1rem;
  margin-top: 1rem;
}
.a-pdf-editPdf .ekspanderbartPanel__innhold {
  padding: 0px;
}
.a-pdf-editPdf .rc-collapse-item-active .rc-collapse-header {
  background-color: aliceblue;
}
.a-pdf-editPdf .rc-collapse-item:not(.rc-collapse-item-active) .rc-collapse-header {
  background-color: white;
}
.a-pdf-editPdf .rc-collapse-item:not(.rc-collapse-item-active) .rc-collapse-header:hover {
  background-color: lightblue;
}
.a-pdf-editPdf .rc-collapse-content {
  padding: 0px;
}
.a-pdf-editPdf .rc-collapse-content-box {
  margin: 0px !important;
}
.a-pdf-editPdf .dndtargets {
  position: sticky;
  top: 1rem;
}
.a-pdf-generatePDF .hiddenLink {
  position: absolute;
  left: -9999px;
}
.a-pdf-generatePDF .hjelpetekst {
  position: absolute;
  right: 1rem;
  margin-top: 1rem;
}
.a-pdf-generatePDF .downloadButton {
  position: relative;
  top: 40%;
}
.a-pdf-generatePDF .pdfrow {
  display: flex;
  align-items: center;
}
