.a-pdf-DnDPage {
  display: inline-block;
}
.a-pdf-DnDPage .pageNumber {
  position: absolute;
  bottom: 0px;
  right: 0px;
  z-index: 2;
  background-color: white;
  padding: 0px 3px;
  border-radius: 3px;
}
.a-pdf-DnDPage .document {
  position: relative;
}
.a-pdf-DnDPage .icon {
  position: absolute;
  z-index: 10;
  background-color: white;
  padding: 0px 5px;
  border-radius: 3px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  top: 0px;
}
.a-pdf-DnDPage .icon:hover {
  background-color: lightblue;
}
.a-pdf-DnDPage .icon.actionIcon {
  right: 0px;
}
.a-pdf-DnDPage .icon.previewIcon {
  right: 30px;
}
.a-pdf-DnDPage .page {
  display: inline-block;
  border: none;
  box-shadow: 3px 3px 3px lightgray;
  background: linear-gradient(-45deg, #ffffff, #ffffff), linear-gradient(45deg, #ffffff, #ffffff), linear-gradient(135deg, transparent 7%, #ffffff 7%);
  background-size: 50% 0%, 50% 100%, 100% 100%;
  background-position: 0 0, 100% 100%, 100% 0;
  background-repeat: no-repeat;
}
.a-pdf-DnDPage .page:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 16%;
  height: 11%;
  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.3), 2px 2px 2px rgba(128, 128, 128, 0.2);
  background: linear-gradient(135deg, transparent 50%, #ffffff 50%);
}
.a-pdf-DnDPage .page .react-pdf__Page__svg {
  background: transparent !important;
}
