.a-pdf-dndTarget .a-pdf-dndTarget-droppable {
  background-color: whitesmoke;
  padding: 10;
  overflow-y: auto;
  height: 65vh;
  display: flex;
  flex-direction: column;
  box-shadow: inset 5px 5px 5px lightgrey;
}
.a-pdf-dndTarget .a-pdf-dndTarget-droppable-active {
  background-color: rgba(0, 128, 0, 0.2);
  animation: dndTargetDroppablePulsate 1s ease-out;
  animation-iteration-count: infinite;
}
.a-pdf-dndTarget .a-pdf-dndTarget-draggable {
  margin: 0.5rem 0.5rem 0rem 0.5rem;
}
.a-pdf-dndTarget .a-pdf-dndTarget-draggable-active {
  opacity: 0.8;
  animation: dndTargetDraggablePulsate 1s ease-out;
  animation-iteration-count: infinite;
}
@keyframes dndTargetDroppablePulsate {
  0% {
    background-color: rgba(0, 128, 0, 0.2);
  }
  50% {
    background-color: rgba(0, 128, 0, 0.1);
  }
  100% {
    background-color: rgba(0, 128, 0, 0.2);
  }
}
@keyframes dndTargetDraggablePulsate {
  0% {
    opacity: 0.8;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 0.8;
  }
}
