.a-pdf-dndSpecial {
  position: relative;
}
.a-pdf-dndSpecial .a-pdf-dndSpecial-droppable {
  background-color: whitesmoke;
  padding: 10px;
  display: flex;
  overflow-x: auto;
  white-space: nowrap;
  box-shadow: inset 5px 5px 5px lightgrey;
}
.a-pdf-dndSpecial .a-pdf-dndSpecial-droppable-active {
  background-color: rgba(0, 128, 0, 0.2);
  animation: dndSpecialPulsate 1s ease-out;
  animation-iteration-count: infinite;
}
.a-pdf-dndSpecial .hjelpetekst {
  bottom: 4rem;
}
.a-pdf-dndSpecial .dragging {
  opacity: 0.5;
}
.a-pdf-dndSpecial .textarea--medMeta__teller {
  font-size: 0.75rem !important;
}
.a-pdf-dndSpecial .textarea--medMeta__wrapper {
  width: 100%;
}
.a-pdf-dndSpecial .textarea--medMeta__wrapper textarea {
  min-height: 5rem !important;
}
.a-pdf-dndSpecial .textarea--medMeta__wrapper textarea::placeholder {
  font-size: 0.75rem !important;
}
.a-pdf-dndSpecial .textarea__container {
  margin-bottom: 0rem;
}
.a-pdf-dndSpecial .a-pdf-dndSpecial-draggable-active {
  border: 2px dashed gray;
  padding: 2px;
  animation: dndSpecialPulsateBorder 1s ease-out;
  animation-iteration-count: infinite;
}
.a-pdf-dndSpecial .cloneStyle + div {
  display: none !important;
}
@keyframes dndSpecialPulsate {
  0% {
    background-color: rgba(0, 128, 0, 0.2);
  }
  50% {
    background-color: rgba(0, 128, 0, 0.1);
  }
  100% {
    background-color: rgba(0, 128, 0, 0.2);
  }
}
@keyframes dndSpecialPulsateBorder {
  0% {
    border: 2px dashed #808080;
  }
  50% {
    border: 2px dashed rgba(128, 128, 128, 0);
  }
  100% {
    border: 2px dashed #808080;
  }
}
